@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600&display=swap');

:root {
  --main-color: #443;
  --border-radius: 95% 4% 97% 5%/4% 94% 3% 95%;
  --border-radius-hover: 4% 95% 6% 95%/95% 4% 92% 5%;
  --border: 0.2rem solid var(--main-color);
  --border-hover: 0.2rem dashed var(--main-color);
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  box-shadow: none;
  font-family: 'Poppins', sans-serif;
  text-decoration: none;
  transition: all 0.2s linear;
  text-transform: capitalize;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-padding-top: 6rem;
  scroll-behavior: smooth;
}

html::-webkit-scrollbar {
  width: 1rem;
  height: 0.5rem;
}

html::-webkit-scrollbar-thumb {
  background: #666;
}

html::-webkit-scrollbar-track {
  background: transparent;
}

.btn {
  display: inline-block;
  padding: 0.9rem 1.5rem;
  border-radius: var(--border-radius);
  color: var(--main-color);
  border: var(--border);
  cursor: pointer;
  margin-top: 1rem;
  font-size: 1.7rem;
}

.btn:hover {
  border: var(--border-hover);
  border-radius: var(--border-radius-hover);
}

#menu-btn {
  font-size: 3rem;
  color: var(--main-color);
  cursor: pointer;
  display: none;
}

header {
  display: flex;
  height: 10vh;
  align-items: center;
  justify-content: space-between;
  padding: 0.2rem 9%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: #ffff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}

header .logo {
  font-size: 3rem;
  font-weight: bolder;
  color: var(--main-color);
}

header .logo i {
  font-size: 2.5rem;
  color: brown;
  padding: 0.5rem;
}

header .navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 35%;
}

header .navbar a {
  font-size: 1.8rem;
  color: var(--main-color);
  margin: 0 1rem;
  text-decoration: none;
  text-transform: capitalize;
}

header .btn {
  margin-top: 0;
}

section {
  padding: 2rem 9%;
}

.heading {
  font-size: 6rem;
  text-transform: uppercase;
  color: #aaa;
  -webkit-text-stroke: 0.05rem var(--main-color);
  letter-spacing: 0.2rem;
  text-align: center;
  pointer-events: none;
  position: relative;
}

/*home section styling*/
.home {
  padding-top: 12rem;
  min-height: 100vh;
  background: url(../public/assets/images/home-bg.jpg) no-repeat;
  background-position: center;
  background-size: cover;
}

.home .row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.home .row .content {
  flex: 1 1 42rem;
}

.home .row .content h3 {
  font-size: 6.5rem;
  color: var(--main-color);
  text-transform: uppercase;
}

.home .row .img {
  flex: 1 1 42rem;
  padding-top: 10rem;
  text-align: center;
}

.home .row .img img {
  height: 35rem;
  animation: float 4s linear infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0rem);
  }
  50% {
    transform: translateY(-7rem);
  }
}

.home .image-slider {
  text-align: center;
  padding: 3rem 0;
}

.home .image-slider img {
  height: 9rem;
  margin: 0 0.5rem;
  cursor: pointer;
  margin-top: 10rem;
}

.home .image-slider img:hover {
  transform: translateY(-2rem);
}

/*about styling*/
.about .heading span {
  font-size: 3rem;
  -webkit-text-stroke: 0.07rem black;
  color: transparent;
}

.about .row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2.5rem;
}

.about .row .image {
  flex: 1 1 42rem;
}

.about .row .image img {
  width: 100%;
  animation: aImage 4s linear infinite;
}

@keyframes aImage {
  0%,
  100% {
    transform: scale(0.9);
    border-radius: var(--border-radius-hover);
  }
  50% {
    transform: scale(0.8);
    border-radius: var(--border-radius);
  }
}

.about .row .content {
  flex: 1 1 42rem;
}

.about .row .content .title {
  color: var(--main-color);
  font-size: 3rem;
  line-height: 1.8;
}

.about .row .content p {
  color: var(--main-color);
  font-size: 1.5rem;
  padding: 1rem 0;
  line-height: 1.8;
}

.about .row .content .icons-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 1rem;
  margin-top: 3rem;
}

.about .row .content .icons-container .icons {
  flex: 1 1 15rem;
  padding: 1.5rem;
  text-align: center;
  border: var(--border);
  border-radius: var(--border-radius);
}

.about .row .content .icons-container .icons img {
  height: 5rem;
}

.about .row .content .icons-container .icons h3 {
  font-size: 1.7rem;
  padding-top: 1rem;
  color: var(--main-color);
}

/*menu styling*/
.menu .heading span {
  font-size: 3rem;
  -webkit-text-stroke: 0.07rem black;
  color: transparent;
}

.menu {
  background: url(../public/assets/images/menu-bg.jpg) no-repeat;
  background-position: center;
  background-size: cover;
}

.menu .box-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

.menu .box-container .box {
  flex: 1 1 20rem;
  padding: 2rem;
  border: var(--border);
  border-radius: var(--border-radius);
  display: flex;
  align-items: center;
  gap: 1.5rem;
  margin-left: 6rem;
}

.menu .box-container .box:hover {
  border: var(--border-hover);
  border-radius: var(--border-radius-hover);
}

.menu .box-container .box img {
  height: 8rem;
}

.menu .box-container .box .content h3 {
  font-size: 2.2rem;
  color: var(--main-color);
  line-height: 1.8;
}

.menu .box-container .box .content p {
  font-size: 1.6rem;
  color: var(--main-color);
  line-height: 1.8;
  padding: 1rem 0;
}

.menu .box-container .box .content span {
  font-size: 2.5rem;
  color: brown;
  line-height: 1.8;
}

/*reviews styling*/
.review .heading span {
  font-size: 3rem;
  -webkit-text-stroke: 0.07rem black;
  color: transparent;
}

.review .review-slider {
  padding: 7rem 3rem;
}

.review .review-slider .box {
  border: var(--border);
  border-radius: var(--border-radius);
  text-align: center;
  position: relative;
  z-index: 0;
  padding: 2rem;
}

.review .review-slider .box .fa-quote-left {
  position: absolute;
  top: 2rem;
  left: 2.5rem;
  font-size: 6rem;
  color: #ccc;
  z-index: -1;
}

.review .review-slider .box .fa-quote-right {
  position: absolute;
  bottom: 2rem;
  right: 2.5rem;
  font-size: 6rem;
  color: #ccc;
  z-index: -1;
}

.review .review-slider .box:hover .fa-quote-left {
  top: -6.5rem;
}

.review .review-slider .box:hover .fa-quote-right {
  bottom: -6.5rem;
}

.review .review-slider .box img {
  height: 7rem;
  width: 7rem;
  border-radius: 50%;
  margin-bottom: 0.7rem;
}

.review .review-slider .box .stars {
  padding: 0.5rem 0;
}

.review .review-slider .box .stars i {
  font-size: 1.7rem;
  color: var(--main-color);
}

.review .review-slider .box p {
  font-size: 1.6rem;
  color: var(--main-color);
  padding: 1rem 0;
  line-height: 1.8;
}

.review .review-slider .box h3 {
  font-size: 2.2rem;
  color: var(--main-color);
  line-height: 1.8;
}

.review .review-slider .box span {
  font-size: 1.5rem;
  color: var(--main-color);
}

.swiper-pagination-bullet {
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 0;
}

.swiper-pagination-bullet-active {
  background: var(--main-color);
}

/*bookings styling*/
.book {
  background: url(../public/assets/images/book-bg.jpg) no-repeat;
  background-position: center;
  background-size: cover;
}

.book .heading span {
  font-size: 3rem;
  -webkit-text-stroke: 0.07rem black;
  color: transparent;
}

.book form {
  margin: 0 auto 2rem auto;
  max-width: 60rem;
  border-radius: var(--border-radius-hover);
  padding: 3rem;
  border: var(--border);
}

.book form .box {
  width: 95%;
  padding: 1rem 1.2rem;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  background: none;
  text-transform: none;
  color: var(--main-color);
  border: var(--border);
  margin: 0.7rem 0;
}

.book form .box:focus {
  border: var(--border-hover);
}

.book form .box textarea {
  height: 15rem;
  resize: none;
}

/*footer styling*/
.footer .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(23rem, 1fr));
  gap: 1.5rem;
}

.footer .box-container .box h3 {
  font-size: 2.5rem;
  padding: 1rem 0;
  color: var(--main-color);
}

.footer .box-container .box a {
  display: block;
  font-size: 1.5rem;
  padding: 1rem 0;
  color: var(--main-color);
}

.footer .box-container .box a i {
  padding-right: 0.5rem;
}

.footer .box-container .box a:hover i {
  padding-right: 2rem;
}

.footer .credit {
  text-align: center;
  font-size: 2rem;
  padding: 2rem 1rem;
  margin-top: 1rem;
  color: var(--main-color);
}

.footer .credit span {
  border-bottom: var(--border-hover);
}

/*media queries*/
@media (max-width: 991px) {
  html {
    font-size: 55%;
  }

  header {
    padding: 0 2rem;
  }

  section {
    padding: 2rem;
  }
}

@media (max-width: 768px) {
  #menu-btn {
    display: initial;
  }

  #menu-btn.fa-times {
    transform: rotate(180deg);
  }

  header .navbar {
    position: absolute;
    top: 99%;
    left: 0;
    right: 0;
    background: #fff;
    display: block;
    width: 100%;
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }

  header .navbar.active {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }

  header .navbar a {
    display: block;
    padding: 1.5rem 2rem;
    margin: 0;
    font-size: 2.1rem;
  }

  .home {
    text-align: center;
    height: fit-content;
  }

  .home .row .content h3 {
    font-size: 4rem;
  }

  .home .row .img img:hover {
    transform: translateY(-4rem);
  }

  .home .row .img img {
    height: 27rem !important;
    width: 23rem !important;
  }

  .home .image-slider img {
    margin-top: 7rem;
  }

  .heading {
    font-size: 4rem;
  }

  .heading span {
    font-size: 2.5rem;
  }

  .menu .box-container .box {
    margin-left: 1.5rem;
  }

  .menu .box-container .box img {
    margin-left: -5rem;
  }
}

@media (max-width: 450px) {
  html {
    font-size: 50%;
  }

  .home .row .img img {
    height: auto;
    width: 100%;
  }
}
